html,
body,
#root,
#app {
  height: 100%;
  width: 100%;
}
* {
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", Arial, Helvetica, sans-serif;
  margin: 0;
}
body #app {
  height: 100%;
  width: 100%;
  min-width: 320px;
}
body #app .main-content {
  position: absolute;
  top: 3em;
  width: 100%;
  height: calc(100% - 3em);
  padding: 2em;
}
